
















































































import { Component, Vue } from "vue-property-decorator";
import AdminTable from "@/components/admin/AdminTable.vue";

@Component({
  components: {
    AdminTable
  }
})
export default class AdminSteamBotProxy extends Vue {
  creationProxyList = "";
  creationProxyComment = "";
  creationProxyDialog = false;
  columns = [
    {
      name: "_id",
      required: true,
      label: "Steam ID",
      field: "_id",
      align: "left",
      type: "string"
    },
    {
      name: "url",
      align: "center",
      label: "url",
      field: "url",
      editable: true,
      type: "string"
    },
    {
      name: "steamBotId",
      align: "center",
      label: "steamBotId",
      field: "steamBotId",
      editable: true,
      type: "string"
    },
    {
      name: "comment",
      align: "center",
      label: "Комментарий",
      field: "comment",
      editable: true,
      type: "string"
    },
    {
      name: "enabled",
      align: "center",
      label: "enabled",
      field: "enabled",
      editable: true,
      type: "boolean"
    }
  ];

  async submit(mutate) {
    const urls = this.creationProxyList.split("\n").map(proxy => {
      const [ip, port, login, password] = proxy.split(":");
      return `socks5://${login}:${password}@${ip}:${port}`;
    });

    for await (const url of urls) {
      try {
        await mutate({
          variables: { url, comment: this.creationProxyComment }
        });
      } catch (e) {
        console.error(e);
      }
    }
  }
}
